<template>
<div id="rentCar">
   <h2>商家如何提现？</h2>
  <p>在工作台点击对账中心，进入对账中心页面，可查看收益数据，可进行提现操作。</p>
  <p>提现需要完成提现密码设置，密码设置完成后可进行修改。 </p>
  <p>今日收益=天天租用户提车后订单金额+分时租用户完成订单金额 </p>
  <p>今日浏览=用户访问店铺次数</p>
  <p>今日订单金额=今日产生租车订单总金额，包括所有状态的天天租车订单、分时租车订单。今日完成订单金额=今日已完成天天租车、分时租车订单</p>
  <p>以上收益及订单金额，均不包含押金。</p>
  <div class="imgDiv">
    <img src="../../assets/m8.png" alt="">
  </div>
  <div class="imgDiv">
    <img src="../../assets/m9.jpg" alt="">
  </div>
</div>
</template>
<script>
    export default {
        name: "rentCar"
    }
</script>
<style scoped lang="scss">

</style>



